<template>
	<div class="text_wapper">
		<h1 class="title"></h1>
		<div class="text">
			<p>{{text}}</p>
		</div>
	</div>
</template>

<script>
	export default{
		name:'index',
		data() {
			return {
				showalert:false,
				text:' '
			}
		},
		methods:{
			button(){
				this.$message("")
			},
			alertbutton(){
				this.showalert=true
				return this.showalert
			},
			closealert(){
				this.showalert=false
			}
		}
	}
</script>
<style lang="less" scoped>
	.text_wapper{
		min-height: 300px;
		width:77%;
		padding:20px;
		background: rgb(173, 149, 69);
		margin:50px auto;
		border-radius: 5px;
		box-shadow: 0 2px 12px 0 rgba(0,0,0,.1);
		background-color:#fff;
		background-image: linear-gradient(#FFF, #cfe6d5);
		h1{
			line-height: 70px;
			color:rgb(78, 77, 77);
		}
		.text{
			font-size: 14px;
			p{
				text-indent: 50px;
				text-align: left;
				line-height: 24px;
			}
		}
	}
</style>



